import { Header,Container, Main, Button, Row, Col, Input, Table, TableColumn, Dialog, 
  Form, FormItem,  Select, Option, Cascader, Checkbox, MessageBox, Message} from 'element-ui';
import store from './store';
import 'element-ui/lib/theme-chalk/index.css';

Vue.config.productionTip = false
Vue.use(Header)
Vue.use(Container)
Vue.use(Main)
Vue.use(Button)
Vue.use(Row)
Vue.use(Col)
Vue.use(Input)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(Form)
Vue.use(Option)
Vue.use(Select)
Vue.use(FormItem)
