<template>
    <el-container>
    <el-header>用户管理</el-header>
    <el-main>
      <!-- 头部区域 -->
      <el-row type="flex">
        <el-col :span="4">
          <el-button type="primary" @click="dialogVisible = true" >新建</el-button> 
        </el-col>
        <el-col :span='8' >
            <el-input v-model="searchKey" placeholder="请输入搜索内容"></el-input>
        </el-col>
        <el-col :span="8">
          <el-button type="primary" @click="searchUser">搜索</el-button> 
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goBack">撤销</el-button> 
        </el-col>
      </el-row>
      <!-- 表单区域 -->
      <el-dialog title="新建/编辑用户"  :visible.sync="dialogVisible"  width="60%" >    
        <el-form ref="form" :model="form" label-width="80px" >
            <el-row>
              <el-col :span="10">
                <el-form-item label="姓名" >
                    <el-input v-model="form.name" ></el-input>
                </el-form-item>
              </el-col>   
              <el-col :span="8">   
                <el-form-item label="性别" >
                  <el-select  v-model='form.sex' placeholder="" >
                    <el-option label="男" value="男"></el-option>
                    <el-option label="女" value="女"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">                
                <el-form-item label='年龄' >
                    <el-input  v-model='form.age'></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="10">  
                <el-form-item label="联系电话" >
                    <el-input v-model="form.phone" ></el-input>
                </el-form-item>
              </el-col>  
            </el-row>
            <el-row >
              <el-col :span="24">
                <div class="block">
                <span class="demonstration" >详细地址</span>
                    <el-cascader
                    :key="modalKey"
                    v-model="address"
                    :options="options"
                    @change="handleChange">
                    </el-cascader>
                    <el-col :span="18" >
                      <el-form-item>
                          <el-input  placeholder='请输入详细地址' v-model="form.detailadr" ></el-input>
                      </el-form-item>
                    </el-col>
                </div>
              </el-col>              
            </el-row>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="cancelEditUser">取 消</el-button>
            <el-button type="primary" @click="addUser" >确 定</el-button>
        </span>
      </el-dialog>
      <!-- 表格区域 -->
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column label="" min-width="55">
              <template slot-scope="scope">
                <!-- `checked` 为 true 或 false -->
                <el-checkbox v-model="scope.row.checked" :label="scope.row" @change="changeChecked(scope.row)">&nbsp;</el-checkbox>
              </template>
          </el-table-column>
          <el-table-column v-for="item in tableLabel" :label="item.label" :key='item.prop' :width="item.width ? item.width:125">
              <template slot-scope="scope">
                  <span style="margin-left: 10px">{{ scope.row[item.prop] }}</span>
              </template>
          </el-table-column>
          <el-table-column label="操作" min-width="180">
              <template slot-scope="scope">
                  <el-button style="mini" @click="handleEdit(scope.row)">编辑</el-button>
                  <el-button  style="mini" type="danger" @click="delUser(scope.row)">删除</el-button>
              </template>
          </el-table-column>
      </el-table>
      <!-- 底部区域 -->
      <el-row type="flex">
        <el-col :span='6'>
          <el-button  style="mini" type="danger" @click="delMulUser()">批量删除</el-button>
        </el-col>
      </el-row>        
    </el-main>
    </el-container>
</template>

<script>
import { mapState } from 'vuex';
export default {
    data() {
      return {
        dialogVisible:false,
        searchKey:'',//搜索
        searchList:[],//搜索返回列表      
        searchEdit:false,//搜索后编辑
        dialogTitle:'',//判断是新增还是编辑
        modalKey:0,//级联选择器重新渲染
        checkedList:[],//多选清单
        address:[],//地区地址
        form: {
          checked:'',
          name: '',
          sex: '',
          age:'',
          phone: '',
          address: '',
          detailadr:'',
          },
        tableLabel:[
          {
            prop: 'name',
            label: '姓名'
          },
          {
            prop: 'age',
            label: '年龄'
          },
          {
            prop: 'sex',
            label: '性别'
          },
          {
            prop: 'phone',
            label: '手机号码',
            width: 200
          },
          {
            prop: 'address',
            label: '地址',
            width: 520
          }]
          ,
        options: [{
          value: '广东省',
          label: '广东省',
          children: [{
            value: '广州市',
            label: '广州市',
            children: [{
              value: '番禺区',
              label: '番禺区'
            }, {
              value: '越秀区',
              label: '越秀区'
            }, {
              value: '花都区',
              label: '花都区'
            }, {
              value: '天河区',
              label: '天河区'
            },
            {
              value: '海珠区',
              label: '海珠区'
            }]
          }]
        }]
    } 
    },
    methods: {
      //点击编辑显示Form表单
      handleEdit(value){
        this.dialogTitle = 'edit'
        this.dialogVisible = true
        let pro = value.address.slice(0,3)
        let city = value.address.slice(3,6)
        let state = value.address.slice(6,9)
        this.address.push(pro,city,state)
        this.modalKey++
        this.form = JSON.parse(JSON.stringify(value))
        console.log(this.form,'form')
      },
      //级联选择器地区处理
       handleChange(value) {
        this.form.address = value.join('')
      },
      //多选按钮
      changeChecked(value){
        if(value.checked){
            this.checkedList.push(value)
            }      
      },
      //判断是否正确填写用户信息
      judgeUserInfo(form){
        //年龄、手机号码正则规则)
        let reg = new RegExp('^[0-9]*$')
        //判断表单提交是否有空值
          if(form.age === '' || form.name === ''|| form.address === ''
            || form.phone === ''|| form.detailadr === ''|| form.sex === '' ){
                alert('请输入完整信息')
          }else if(reg.test(form.age) !==true || reg.test(form.phone) !==true || reg.test(form.name)){
            alert('请输入正确的年龄或手机号码或姓名') 
          }else if(this.dialogTitle =='edit' ){
            this.dialogVisible = false
            this.address = []
            this.dialogTitle =''
            this.$message({
              type: 'success',
              message: '已编辑完成'
            })
            sessionStorage.setItem('goBackList',JSON.stringify(this.tableData))
            this.editUser(form)
          }else{
            this.dialogVisible = false
            this.address = []
            this.$message({
              type: 'success',
              message: '已添加成功'
            })
            sessionStorage.setItem('goBackList',JSON.stringify(this.tableData))
            this.$store.commit('addUser',form)
          }
      },
      //取消编辑
      cancelEditUser(){
        this.dialogVisible = false
      },
      //编辑确认处理
      editUser(value){
        this.tableData.some(item =>{
          if(item.id==value.id){
              item.address = value.address.slice(0,9) + value.detailadr
              item.age = value.age
              item.checked = value.checked
              item.detailadr= value.detailadr
              item.name = value.name
              item.sex= value.sex
              item.phone = value.phone
            }
        })
      },
      //新建/编辑用户确认按钮
      addUser(){
            this.form.checked = false
            //判断是新建还是编辑
            if(this.dialogTitle =='edit'){ 
              //判断是否通过搜索再编辑
              if(this.searchEdit ==true){
                this.handleSearchEdit()
              }else{
                this.judgeUserInfo(this.form)
                sessionStorage.setItem('tableData',JSON.stringify(this.tableData))
              }
            }else{
              this.form.id =this.id
              this.form.address += this.form.detailadr
              this.judgeUserInfo(this.form)
              sessionStorage.setItem('tableData',JSON.stringify(this.tableData))
            }           
      },
      //删除按钮
      delUser(value) {
        this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
          }).then(() => {
            sessionStorage.setItem('goBackList',JSON.stringify(this.tableData))
            this.tableData.splice(
                this.tableData.indexOf(this.tableData.find(element=>{
                  return element.id === value.id })
            ),1)
            sessionStorage.setItem('tableData',JSON.stringify(this.tableData))           
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });          
          });
      },
      //批量删除
      delMulUser(){
        if(this.checkedList.length !==0 ){
        this.$confirm('此操作将永久删除选中用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
          }).then(() => {
            this.checkedList.forEach(listItem =>{
              this.tableData.splice(this.tableData.indexOf(this.tableData.find(item =>{
                return item.id === listItem.id  })
              ),1)
            })
            sessionStorage.setItem('tableData',JSON.stringify(this.tableData))           
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });          
          })}else{
            this.$message('未选择用户，请选择用户再删除')
          }
      },
      //关键词搜索
      searchUser(){
        if(this.searchKey == ''){
          alert('请输入搜索内容')
        }else{
          this.searchEdit = true
          var reg = new RegExp(this.searchKey)
          this.tableData.find(item=>{
                if(reg.test(item.age) || reg.test(item.name) || reg.test(item.sex) 
                || reg.test(item.address) || reg.test(item.detailadr) || reg.test(item.phone)){
                  this.searchList.unshift(item)
                  this.$store.commit('updataTableData',JSON.parse(JSON.stringify(this.searchList)))
                }         
          })
          console.log(this.searchList,'111')
          this.searchList = []
        }
      },
      //搜索后编辑处理
      handleSearchEdit(){
        let searchEditList = JSON.parse(sessionStorage.getItem('tableData'))
                searchEditList.some(item =>{
                  if(item.id == this.form.id){
                      item.address = this.form.address.slice(0,9) + this.form.detailadr
                      item.age = this.form.age
                      item.checked = this.form.checked
                      item.detailadr= this.form.detailadr
                      item.name = this.form.name
                      item.sex= this.form.sex
                      item.phone = this.form.phone
                  }
                })
                console.log(searchEditList,'sel1')
                sessionStorage.setItem('tableData',JSON.stringify(searchEditList))
                this.editUser(this.form)
                this.dialogVisible = false
      },
      //撤销操作
      goBack(){
        this.$store.commit('goBackList',JSON.parse(sessionStorage.getItem('goBackList')))
        sessionStorage.setItem('tableData',JSON.stringify(this.tableData))
        this.$message({
              type: 'success',
              message: '已撤销'
            });
      },
      //分页功能
    },
    //刷新获取用户列表
    created(){
      if(window.sessionStorage.getItem('tableData')!= null){
        this.$store.commit('updataTableData',JSON.parse(sessionStorage.getItem('tableData')))
        this.$store.commit('updataUserId',JSON.parse(sessionStorage.getItem('id')))
        }
    },
    computed:{
      ...mapState(['tableData','id']),
    },
    watch:{
      //弹出层
      dialogVisible(){
        if(this.dialogVisible == false){
          this.address = []
          this.form = {name: '',sex: '',age:'',phone: '',address: '',detailadr:'',checked:''}
        }
      },
      //搜索后还原用户列表
      searchKey(){
        this.searchEdit = false
        this.$store.commit('updataTableData',JSON.parse(sessionStorage.getItem('tableData')))
      }

    }
}
</script>

<style lang='less' >
.el-header{
    background-color:#999;
    text-align: center;
    line-height: 60px;
}
.demonstration{
    margin-left: 0.72rem;
    margin-right: 0.72rem;
}
</style>