import Vue from 'vue';
import  Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        id:3,
        tableData:[
            {
                address: "",
                age: "",
                checked: false,
                detailadr: "",
                id: 0,
                name: "",
                phone: "",
                sex: "",
            },
            {
                address: "",
                age: "",
                checked: false,
                detailadr: "",
                id: 1,
                name: "",
                phone: "",
                sex: "",
            },
            {
                address: "",
                age: "",
                checked: false,
                detailadr: "",
                id: 2,
                name: "",
                phone: "",
                sex: "",
            }
        ]
    },
    mutations:{
        addUser(state,value){
            state.tableData.unshift(value)
            state.id++
            sessionStorage.setItem('id',JSON.stringify(state.id))
        },
        updataTableData(state,value){
            state.tableData = value
        },
        updataUserId(state,value){
            state.id = value
        },
        goBackList(state,value){
            state.tableData = value
        },

    }
})